import React, { useState } from "react";
import Dashboard from "./Dashboard";
import Users from "./Users";

import {
  checkRequiredFields,
  checkEmailFormat,
  authenticateUser,
} from "./ErrorCheckService";
import {
  Container,
  Title,
  Input,
  Button,
  FooterText,
} from "./StyledComponents";

function LoginForm() {
  const [username, setUsername] = useState("");
  const [password, setPassword] = useState("");
  const [error, setError] = useState("");
  const [isLoggedIn, setIsLoggedIn] = useState(false);

  const [studentId, setStudentId] = useState(null); // studentId

  const handleSubmit = async (e) => {
    // async 
    e.preventDefault();

    // 
    const requiredError = checkRequiredFields([username, password]);
    if (requiredError) {
      setError(requiredError);
      return;
    }

    // 
    const emailError = checkEmailFormat(username);
    if (emailError) {
      setError(emailError);
      return;
    }

    // 
    const result = await authenticateUser(username, password); // await 
    if (result !== null) {
      setIsLoggedIn(true);
      setStudentId(result); // studentId
      setIsLoggedIn(true); // isLoggedIntrue
    } else {
      setError("");
      setIsLoggedIn(false); // isLoggedInfalse
    }
  };

  return isLoggedIn ? (
    <Dashboard studentId={studentId} /> // DashboardstudentId
  ) : (
    <Container>
      <Title>Hp</Title>
      {error && <p style={{ color: "red" }}>{error}</p>}
      <Input
        type="text"
        value={username}
        placeholder="ID"
        onChange={(e) => setUsername(e.target.value)}
      />
      <Input
        type="password"
        value={password}
        placeholder=""
        onChange={(e) => setPassword(e.target.value)}
      />
      <Button onClick={handleSubmit}></Button>
      <FooterText>
        Copyright  sole color design Co., Ltd. All Rights Reserved.
      </FooterText>

      {/* Users */}
      <Users />
    </Container>
  );
}

export default LoginForm;
